.main{
  .custom-select {
      position: relative;
      font-family: "Montserrat";
      margin-bottom: 15px;
      select {
        display: none;
      }
  
      .select-selected {
        font-family: Montserrat;
        font-size: 14px;
        font-weight: 500;
        background-color: $white;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 3px;
        &:hover {
          background: rgba(51, 125, 90, 0.1);
        }
        &:after {
          font-family: icomoon;
          color: $green;
          font-size: 6px;
          content: "\e922";
          transform: rotate(180deg);
        }
        &.select-arrow-active {
          border: 1.5px solid $darkGreen;
        }
        &.select-arrow-active:after {
          border-color: transparent transparent $green transparent;
          transform: rotate(0deg);
        }
      }
  
      .select-items div,
      .select-selected {
        color: $darkBlue;
        padding: 14px 16px 10px 15px;
        border: 1px solid transparent;
        border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
        cursor: pointer;
        user-select: none;
        border: 0.5px solid rgba(0, 0, 0, 0.15);
      }
  
      .select-items {
        position: absolute;
        background-color: $white;
        top: 100%;
        left: 0;
        right: 0;
        z-index: 99;
      }
  
      .select-hide {
        display: none;
      }
  
      .select-items .same-as-selected {
        background-color: $darkGreen;
        color: $white;
      }
  
      .select-items div:hover,
      .same-as-selected {
        background-color: $darkGreen;
        color: $white;
      }
    }

    input {
      padding: 12px 20px 8px 15px;
      border: 0.5px solid rgba($color: #000000, $alpha: 0.15);
    }
  }