.main .custom-select {
  position: relative;
  font-family: "Montserrat";
  margin-bottom: 15px;
}
.main .custom-select select {
  display: none;
}
.main .custom-select .select-selected {
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 500;
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 3px;
}
.main .custom-select .select-selected:hover {
  background: rgba(51, 125, 90, 0.1);
}
.main .custom-select .select-selected:after {
  font-family: icomoon;
  color: #93b474;
  font-size: 6px;
  content: "\e922";
  transform: rotate(180deg);
}
.main .custom-select .select-selected.select-arrow-active {
  border: 1.5px solid #337d5a;
}
.main .custom-select .select-selected.select-arrow-active:after {
  border-color: transparent transparent #93b474 transparent;
  transform: rotate(0deg);
}
.main .custom-select .select-items div,
.main .custom-select .select-selected {
  color: #1a2f3f;
  padding: 14px 16px 10px 15px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer;
  user-select: none;
  border: 0.5px solid rgba(0, 0, 0, 0.15);
}
.main .custom-select .select-items {
  position: absolute;
  background-color: #ffffff;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
}
.main .custom-select .select-hide {
  display: none;
}
.main .custom-select .select-items .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}
.main .custom-select .select-items div:hover,
.main .custom-select .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}
.main input {
  padding: 12px 20px 8px 15px;
  border: 0.5px solid rgba(0, 0, 0, 0.15);
}

.about-manufactures-swiper {
  background: #fbfbfb;
  overflow: hidden;
}
.about-manufactures-swiper__container {
  gap: 20px;
  padding-top: 99px;
  padding-bottom: 90px;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 960px) {
  .about-manufactures-swiper__container {
    padding-top: 45px;
    padding-bottom: 45px;
    flex-direction: column-reverse;
    align-items: center;
    gap: 30px;
  }
}
@media (max-width: 600px) {
  .about-manufactures-swiper__container {
    padding-top: 30px;
    padding-bottom: 30px;
  }
}
.about-manufactures-swiper__container .about-manufactures-swiper__text-block {
  max-width: 498px;
  width: 100%;
}
.about-manufactures-swiper__container .about-manufactures-swiper__text-block .about-manufactures-swiper__title {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 800;
  font-size: 45px;
  line-height: 45px;
  letter-spacing: 0.01em;
  margin-top: 30px;
}
@media (max-width: 600px) {
  .about-manufactures-swiper__container .about-manufactures-swiper__text-block .about-manufactures-swiper__title {
    font-size: 25px;
    line-height: 25px;
  }
}
@media (max-width: 960px) {
  .about-manufactures-swiper__container .about-manufactures-swiper__text-block .about-manufactures-swiper__title {
    margin-top: inherit;
  }
}
.about-manufactures-swiper__container .about-manufactures-swiper__text-block .about-manufactures-swiper__description {
  max-width: 481px;
  margin-top: 46px;
  font-family: "Raleway";
  font-weight: 500;
  font-size: 16px;
  line-height: 52px;
  line-height: 26px;
  letter-spacing: 0.01em;
  word-spacing: 0.01em;
}
.about-manufactures-swiper__container .about-manufactures-swiper__text-block .about-manufactures-swiper__description .green {
  color: #337d5a;
  font-family: "Raleway";
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  letter-spacing: 0.01em;
}
@media (max-width: 960px) {
  .about-manufactures-swiper__container .about-manufactures-swiper__text-block .about-manufactures-swiper__description {
    margin-top: 20px;
  }
}
.about-manufactures-swiper__container .about-manufactures-swiper__text-block .rectangle.unbackground {
  margin-top: 43px;
  padding: 17px 42px;
  border-width: 2px;
  max-width: max-content;
  margin-left: 0;
}
@media (max-width: 960px) {
  .about-manufactures-swiper__container .about-manufactures-swiper__text-block .rectangle.unbackground {
    margin-top: 20px;
  }
}
@media (max-width: 600px) {
  .about-manufactures-swiper__container .about-manufactures-swiper__text-block .rectangle.unbackground {
    margin-top: 10px;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
  }
}
.about-manufactures-swiper__container .swiper-container.about-manufactures__swiper {
  overflow: hidden;
  max-width: 700px;
  max-height: 472px;
  width: 100%;
  min-width: 0;
  position: relative;
}
.about-manufactures-swiper__container .swiper-container.about-manufactures__swiper .buttons-block {
  position: absolute;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: space-between;
  bottom: 0;
  left: 0;
  width: 141px;
  height: 58px;
  background-color: #337d5a;
  z-index: 2;
}
.about-manufactures-swiper__container .swiper-container.about-manufactures__swiper .buttons-block .swiper-button-next {
  position: relative;
  color: #ffffff;
  right: 0;
  top: 0;
  width: 100%;
  height: 100%;
  margin: 0;
}
.about-manufactures-swiper__container .swiper-container.about-manufactures__swiper .buttons-block .swiper-button-next::after {
  content: "\e919";
  font-family: icomoon;
  font-size: 18px;
  position: absolute;
}
.about-manufactures-swiper__container .swiper-container.about-manufactures__swiper .buttons-block .swiper-button-prev {
  position: relative;
  color: #ffffff;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  margin: 0;
}
.about-manufactures-swiper__container .swiper-container.about-manufactures__swiper .buttons-block .swiper-button-prev::after {
  content: "\e919";
  font-family: icomoon;
  font-size: 18px;
  transform: rotate(180deg);
  position: absolute;
}
.about-manufactures-swiper__container .swiper-container.about-manufactures__swiper .swiper-slide.about-manufactures__slide {
  width: 100%;
  height: 100%;
}
.about-manufactures-swiper__container .swiper-container.about-manufactures__swiper .swiper-slide.about-manufactures__slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.about-manufactures-swiper__container .swiper-container.about-manufactures__swiper .swiper-slide.about-manufactures__slide.video::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.about-manufactures-swiper__container .swiper-container.about-manufactures__swiper .swiper-slide.about-manufactures__slide.video::after {
  font-family: icomoon;
  content: "\e924";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-45%, -39%);
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 4px;
  width: 61px;
  height: 61px;
  border: 2px solid #ffffff;
  border-radius: 50%;
  z-index: 4;
}
@media (max-width: 600px) {
  .about-manufactures-swiper__container .swiper-container.about-manufactures__swiper .swiper-slide.about-manufactures__slide.video::after {
    width: 45px;
    height: 45px;
  }
}