@import "./default/default.scss";

.about-manufactures-swiper {
  background: #fbfbfb;
  overflow: hidden;
  &__container {
    gap: 20px;
    padding-top: 99px;
    padding-bottom: 90px;
    display: flex;
    justify-content: space-between;
    @media (max-width: $tablet) {
      padding-top: 45px;
      padding-bottom: 45px;
      flex-direction: column-reverse;
      align-items: center;
      gap: 30px;
    }
    @media (max-width: $mobile) {
      padding-top: 30px;
      padding-bottom: 30px;
    }
    .about-manufactures-swiper__text-block {
      max-width: 498px;
      width: 100%;
      .about-manufactures-swiper__title {
        @include rale-800-45;
        margin-top: 30px;
        @media (max-width: $tablet) {
          margin-top: inherit;
        }
      }
      .about-manufactures-swiper__description {
        max-width: 481px;
        margin-top: 46px;
        @include rale-500-16;
        line-height: 26px;
        letter-spacing: 0.01em;
        word-spacing: 0.01em;
        .green {
          color: $darkGreen;
          @include rale-600-16;
        }
        @media (max-width: $tablet) {
          margin-top: 20px;
        }
      }
      .rectangle.unbackground {
        margin-top: 43px;
        padding: 17px 42px;
        border-width: 2px;
        max-width: max-content;
        margin-left: 0;
        @media (max-width: $tablet) {
          margin-top: 20px;
        }
        @media (max-width: $mobile) {
          margin-top: 10px;
          text-align: center;
          margin-left: auto;
          margin-right: auto;
        }
        &:hover {
        }
        &:active {
        }
        &:focus {
        }
      }
      br {
      }
    }
    .swiper-container.about-manufactures__swiper {
      overflow: hidden;
      max-width: 700px;
      max-height: 472px;
      width: 100%;
      min-width: 0;
      position: relative;
      .buttons-block {
        position: absolute;
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        justify-content: space-between;
        bottom: 0;
        left: 0;
        width: 141px;
        height: 58px;
        background-color: $darkGreen;
        z-index: 2;
        .swiper-button-next {
          position: relative;
          color: $white;
          right: 0;
          top: 0;
          width: 100%;
          height: 100%;
          margin: 0;
          &::after {
            content: "\e919";
            font-family: icomoon;
            font-size: 18px;
            position: absolute;
          }
        }
        .swiper-button-prev {
          position: relative;
          color: $white;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          margin: 0;
          &::after {
            content: "\e919";
            font-family: icomoon;
            font-size: 18px;
            transform: rotate(180deg);
            position: absolute;
          }
        }
      }
      .swiper-wrapper.about-manufactures__wrapper {
      }
      .swiper-button-next {
      }
      .swiper-button-prev {
      }
      .swiper-slide.about-manufactures__slide {
        width: 100%;
        height: 100%;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
        &.video {
          &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            cursor: pointer;
          }
          &::after {
            font-family: icomoon;
            content: "\e924";
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-45%, -39%);
            color: $white;
            display: flex;
            justify-content: center;
            align-items: center;
            padding-left: 4px;
            width: 61px;
            height: 61px;
            border: 2px solid $white;
            border-radius: 50%;
            z-index: 4;
            @media (max-width: $mobile) {
              width: 45px;
              height: 45px;
            }
          }
        }
      }
    }
  }
}
